/*--------------------------------------------------------------
# Mixins
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# BASE
--------------------------------------------------------------*/
html,
body {
  width: 100%;
  height: 100%; }

.site {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch; }

.site-main {
  flex-grow: 1; }

.site-header,
.site-main,
.site-footer {
  flex-shrink: 0; }
